.ProgressBlocks {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1em;
}
.ProgressBlock {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 40px;
  margin-left: 0.5em;
  margin-right: 0.5em;
  background-color: #fff;
  color: #fff;
  font-weight: bold;
  line-height: 40px;
  text-align: center;
}
.ProgressBlock:first-child {
  margin-left: 0;
}
.ProgressBlock:last-child {
  margin-right: 0;
}
.ProgressBlock.is-done {
  background-color: #76b900;
  background-image: url("../assets/checkmark.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.ProgressBlock.is-active {
  background-color: #ffb200;
  box-shadow: 0 0 10px #ffb200;
}


